import React from "react";
import PropTypes from "prop-types";
import Helmet from "react-helmet";

/**
 * Open Graph metadata
 * @param {object} meta
 * @link https://ogp.me/
 */
const ogMeta = (meta) => {
  const metaList = [
    {
      property: "og:title",
      content: meta.title || meta.siteTitle,
    },
    {
      property: "og:description",
      content: meta.description,
    },
    {
      property: "og:type",
      content: meta.type,
    },
  ];

  if (meta.tags?.length > 0) {
    metaList.push({
      name: "keywords",
      content: meta.tags.join(", "),
    });

    if (meta.type === "article") {
      metaList.push({
        name: "article:tag",
        content: meta.tags.join(", "),
      });
    }
  }

  if (meta.type === "article") {
    meta.author &&
      metaList.push({
        name: "article:author",
        content: meta.author,
      });

    meta.category &&
      metaList.push({
        name: "article:section",
        content: meta.category,
      });
  }

  return metaList;
};

/**
 * Twitter metadata
 * @param {object} meta
 * @link https://developer.twitter.com/en/docs/twitter-for-websites/cards/guides/getting-started
 */
const twitterMeta = (meta) => {
  return [
    {
      name: "twitter:card",
      content: "summary",
    },
    {
      name: "twitter:creator",
      content: meta.author,
    },
    {
      name: "twitter:title",
      content: meta.siteTitle,
    },
    {
      name: "twitter:description",
      content: meta.description,
    },
  ];
};

/**
 * Seo
 * @param {object} props
 * @description
 * add some SEO elements thanks to React-helmet
 */
function SEO(props) {
  return (
    <Helmet
      htmlAttributes={{
        lang: props.lang,
      }}
      title={props.title || props.siteTitle}
      titleTemplate={`%s | ${props.siteTitle}`}
      meta={[
        {
          name: "description",
          content: props.description,
        },
        ...ogMeta(props),
        ...twitterMeta(props),
      ]}
    />
  );
}

SEO.propTypes = {
  siteTitle: PropTypes.string.isRequired,
  title: PropTypes.string,
  description: PropTypes.string.isRequired,
  author: PropTypes.string.isRequired,
  category: PropTypes.string,
  lang: PropTypes.string,
  tags: PropTypes.array,
  type: PropTypes.string,
};

SEO.defaultProps = {
  title: "",
  lang: "fr",
  type: "website",
  tags: null,
  category: null,
};

export default SEO;
